/* Live Image Feed */
.live-image-feed {
	margin: 20px 0;
	background: #f8f9fa;
	border-radius: 8px;
	padding: 15px;
	border: 1px solid #e1e4e8;
}

.feed-header {
	display: flex;
	align-items: center;
	gap: 8px;
	margin-bottom: 12px;
	font-size: 14px;
	font-weight: 600;
	color: #1d2327;
}

.feed-icon {
	font-size: 18px;
}

.feed-count {
	margin-left: auto;
	font-size: 12px;
	color: #646970;
	font-weight: 400;
}

.feed-scroll-container {
	display: flex;
	gap: 12px;
	overflow-x: auto;
	padding: 8px 0;
	scroll-behavior: smooth;
}

.feed-scroll-container::-webkit-scrollbar {
	height: 6px;
}

.feed-scroll-container::-webkit-scrollbar-track {
	background: #e1e4e8;
	border-radius: 3px;
}

.feed-scroll-container::-webkit-scrollbar-thumb {
	background: #8c8f94;
	border-radius: 3px;
}

.feed-item {
	flex: 0 0 120px;
	animation: slideInFade 0.5s cubic-bezier(0.16, 1, 0.3, 1);
}

@keyframes slideInFade {
	from {
		opacity: 0;
		transform: scale(0.8) translateY(-10px);
	}
	to {
		opacity: 1;
		transform: scale(1) translateY(0);
	}
}

.feed-item-image {
	position: relative;
	width: 120px;
	height: 80px;
	border-radius: 6px;
	overflow: hidden;
	background: #fff;
	border: 2px solid #00a32a;
	box-shadow: 0 2px 8px rgba(0, 163, 42, 0.15);
}

.feed-item-image img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.feed-item-overlay {
	position: absolute;
	top: 0;
	right: 0;
	background: rgba(0, 163, 42, 0.9);
	color: white;
	padding: 2px 6px;
	font-size: 12px;
	border-bottom-left-radius: 4px;
}

.feed-item-badge {
	margin-top: 6px;
	text-align: center;
}

.provider-badge {
	display: inline-block;
	font-size: 10px;
	padding: 2px 8px;
	border-radius: 10px;
	font-weight: 600;
}

.provider-badge.openai {
	background: #10a37f;
	color: white;
}

.provider-badge.google {
	background: #4285f4;
	color: white;
}

.feed-item-alt {
	margin-top: 4px;
	font-size: 11px;
	color: #646970;
	text-align: center;
	line-height: 1.3;
	height: 28px;
	overflow: hidden;
}

/* Empty state */
.live-feed-empty {
	text-align: center;
	padding: 30px;
	color: #646970;
}

.empty-icon {
	font-size: 48px;
	margin-bottom: 10px;
	opacity: 0.5;
}

.empty-text {
	font-size: 13px;
	font-style: italic;
}
