/* Message Rotator */
.message-rotator {
	margin: 20px 0;
	min-height: 60px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.message-content {
	display: flex;
	align-items: center;
	gap: 12px;
	padding: 16px 24px;
	background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
	border-radius: 12px;
	color: white;
	font-size: 15px;
	font-weight: 500;
	box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
	transition: opacity 0.3s ease, transform 0.3s ease;
}

.message-content.fade-in {
	opacity: 1;
	transform: translateY(0);
}

.message-content.fade-out {
	opacity: 0;
	transform: translateY(-10px);
}

.message-icon {
	font-size: 24px;
	animation: bounce 2s infinite;
}

@keyframes bounce {
	0%, 100% {
		transform: translateY(0);
	}
	50% {
		transform: translateY(-5px);
	}
}

.message-text {
	line-height: 1.4;
}
