/**
 * Speed Mode Setup Modal Styles
 */

.speed-mode-setup-overlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: rgba(0, 0, 0, 0.7);
	display: flex;
	align-items: center;
	justify-content: center;
	z-index: 999999;
	animation: fadeIn 0.2s ease;
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

.speed-mode-setup-modal {
	background: #fff;
	border-radius: 8px;
	box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
	max-width: 900px;
	width: 90%;
	max-height: 90vh;
	overflow-y: auto;
	padding: 24px;
	animation: slideUp 0.3s ease;
}

@keyframes slideUp {
	from {
		transform: translateY(30px);
		opacity: 0;
	}
	to {
		transform: translateY(0);
		opacity: 1;
	}
}

.speed-mode-setup-modal h2 {
	margin: 0 0 10px 0;
	font-size: 22px;
	font-weight: 600;
	color: #1d2327;
}

.speed-mode-setup-modal .modal-description {
	margin: 0 0 20px 0;
	font-size: 14px;
	color: #50575e;
	line-height: 1.6;
}

.speed-mode-setup-modal .modal-actions {
	display: flex;
	justify-content: flex-end;
	gap: 8px;
	margin-top: 20px;
	padding-top: 20px;
	border-top: 1px solid #dcdcde;
}

.speed-mode-setup-modal .button {
	padding: 8px 16px;
	font-size: 14px;
	border-radius: 4px;
	cursor: pointer;
	transition: all 0.2s ease;
}

.speed-mode-setup-modal .button-primary {
	background: #2271b1;
	color: #fff;
	border: 1px solid #2271b1;
}

.speed-mode-setup-modal .button-primary:hover {
	background: #135e96;
	border-color: #135e96;
}

.speed-mode-setup-modal .button-secondary {
	background: #fff;
	color: #2271b1;
	border: 1px solid #2271b1;
}

.speed-mode-setup-modal .button-secondary:hover {
	background: #f0f6fc;
	border-color: #135e96;
	color: #135e96;
}
